
.wrapper {
  display: -webkit-flex;
  display: flex;
  width: 100vw;
}

.content {
  width: 100vw;
}


nav.navbar {
  margin-bottom: 1em;
}

img.menu-icon {
  width: 1.5em;
  margin: .3em 1em .3em .3em;
  cursor: pointer;
}

:root {
    --sidebarWidth: 300px;
    --sidebarBackground: #404E67;
    --sidebarActiveLink: #F8877C;
    --sidebarLink: #FDFDFE;
}


#sidebar {
    width: 300px;
    width: var(--sidebarWidth);
    height: 100vh;


    top: 0;
    left: 0;
    z-index: 999;
    margin-left: calc(-1 * 300px);
    margin-left: calc(-1 * var(--sidebarWidth));

    background: #404E67;

    background: var(--sidebarBackground);
    color: #fff;
    transition: all 0.3s;
}

#sidebar h2.app-title {
    padding: .5em;
}

#sidebar.open {
    margin-left: 0;
}

li.sidebar-link {
    color: #FDFDFE;
    color: var(--sidebarLink);
    cursor: pointer;
    background-color: transparent;
    background-color: initial;
}

li.sidebar-link a.active {
    color: #F8877C;
    color: var(--sidebarActiveLink);
    background-color: transparent;
    background-color: initial;
    border: none;
}



.form-buttons button.submit-project-form {
    max-height: 38px;
}

div.project-form-container {
    margin-bottom: 1em;
}




div.modal-footer .btn-toolbar {
    width: 100%;
}

div.container.full-width {
    max-width: none;
    max-width: initial;
}

.full-height {
    height: 100%;
}


div.editor-left-sidebar {
    min-width: 6em;
    -webkit-flex-grow: 0;
            flex-grow: 0;
}

div.editor-center {
    -webkit-flex-grow: 2;
            flex-grow: 2;
}

div.editor-right-sidebar {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    max-width: 25em;
}


.light-background {
    background-color: var(--lightBackground);
}

div.workflow-editor-container {
    -webkit-flex: 1 1;
            flex: 1 1;
    margin-top: -1em;
}

.graph-editor-container {
    display: -webkit-flex;
    display: flex;
    padding: 0;
    position: relative;

    max-width: none;

    max-width: initial;
    -webkit-flex-grow: 2;
            flex-grow: 2;
}

.workflow-breadcrumb {
    position: absolute;
    top: 0;

    margin-left: 15px;
    padding-top: 15px;
}

.workflow-bottom-navbar {
    position: absolute;
    bottom: 0;

    margin-left: 15px;
    width: calc(100% - 30px);

    border-radius: .25em;
}

div.editor-right-sidebar {
    display: -webkit-flex;
    display: flex;
}


div.right-sidebar-parameter {
    margin-bottom: .25em;
}

.srd-diagram {
    height: 100%;

    background-size: 50px 50px;
    background-image: linear-gradient(0deg, transparent 24%, var(--lightBackgroundTransparent) 25%, var(--lightBackgroundTransparent) 26%, transparent 27%, transparent 74%, var(--lightBackgroundTransparent) 75%, var(--lightBackgroundTransparent) 76%, transparent 77%, transparent), linear-gradient(90deg, transparent 24%, var(--lightBackgroundTransparent) 25%, var(--lightBackgroundTransparent) 26%, transparent 27%, transparent 74%, var(--lightBackgroundTransparent) 75%, var(--lightBackgroundTransparent) 76%, transparent 77%, transparent);
}

.diagram-widget-container {
    -webkit-flex: 1 1;
            flex: 1 1;
}

div.deserialization-error-container {
    display: -webkit-flex;
    display: flex;
    width: 100%;
}

div.deserialization-error {
    margin: auto;
}

div.blocking-context-color-box {
    width: var(--color-box-side);
    height: var(--color-box-side);
    border-radius: 25%;
    float: left;
    margin-right: 8px;
}


.blocking-context-drop-down-item {
    padding: 8px;
}

.card-header > div {
    display: -webkit-flex;
    display: flex;
    cursor: pointer;
}

.card-header i.fa-caret-down {
    margin: auto 0 auto auto;
    transition-duration: 0.5s;
}

.card-header i.fa-exclamation-triangle {
    margin-right: .5em;
    margin-top: .25em;
}


i.fa-caret-down.expanded {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}

.block-card {
    margin-bottom: 1em;
}

div.parameter-container-line-separator {
    border-bottom: 1px solid;
    border-bottom-color: var(--separatorColor);
    margin-bottom: .5em;
}

div.parameter-container-line-separator:last-child {
    border-bottom: none;
}

div.block {
    position: relative;
}

div.block div.port.srd-port {
    height: 30px;
}

div.block div.card-header {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    padding-right: 0;
    padding-left: 0;
}

div.flags-container {
    position: absolute;
    right: -5px;
    top: 0;
}

div.flags-container .error {
    color: red;
}

div.block div.block-name {
    margin-left: 8px;
    margin-right: 8px;
}

.format-toggle-group .btn-group {
    width: 100%;
}

.format-toggle-group label {
    cursor: pointer;
}

div.progress-bars-container {
    margin-top: 1em;
    margin-bottom: .5em;
    background-color: #E8ECEF;
}

div.progress-bar-legend {
    margin-bottom: 1em;
}

div.progress-bar-legend div.legend-item {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

div.legend-item div.progress-bar-sample {
    width: 1rem;
    height: 1rem;
    border-radius: .25rem;
    float: left;
    margin-right: .25rem;
}

.sidebar-column {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex: 1 1;
            flex: 1 1;

    max-height: calc(100vh - var(--appBarHeight));
}

.title-and-center-container{
    -webkit-flex: 1 1;
            flex: 1 1;

    overflow-y: auto;
    overflow-x: hidden;
}


:root {
    --color-box-side: 1.25em;
}

div.color-box {
    height: 1.25em;
    height: var(--color-box-side);
    width: 1.25em;
    width: var(--color-box-side);
    border-radius: 25%;
}




.loading-spinner-container {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.table.items-table td,
.table.items-table th {
    padding: 8px;
}

.items-table-container,
.delete-item-table-container {
    overflow-x: auto;
}

.delete-item-table {
    margin-top: .5em;
}

:root {
    --lightBackground: rgb(233, 236, 238);
    --lightBackgroundTransparent: rgba(245, 246, 247, 0.9);

    --appBarHeight: 54px;

    --separatorColor: rgba(0, 0, 0, .2);
}

.light-background,
.navbar.light-background {
    background-color: rgb(233, 236, 238);
    background-color: var(--lightBackground);
}

button.button-link {
    border: none;
    background-color: transparent;
}

/* Tables */
td > a,
td > a:hover {
    color: black;
}

/* Forms */
.form-buttons .btn:first-child {
    margin-right: auto;
}


/* Headers */
div.row.header-row {
    margin-top: .5em;
    margin-bottom: 1em;
}


/* Icons */
a.icon-button {
    margin-right: .5em;
    cursor: pointer;
}


/* hr */
hr {
    border-color: rgba(0, 0, 0, .2);
    border-color: var(--separatorColor);
}

